<template>
  <v-card flat width="100%">
    
    <v-card-text class="text-center">
      <v-divider></v-divider>
      <h2 class="my-5">{{ label }}</h2>
      <v-divider></v-divider>

      <v-data-table
        :headers="headers"
        :items="data"
        :expanded.sync="expanded"
        item-key="collection"
        show-expand
        class="elevation-1"
      >
     
        <template v-slot:expanded-item="{ headers, item }" >
          <td :colspan="headers.length" class="pa-0">
            <v-simple-table >
              <tr>
                <th>Read</th>
                <td>
                   <v-switch
                      @change="handelReadRight($event, item)"
                      v-model="item.read.value"
                    ></v-switch>
                </td>
                <td><v-select label="Own / All" :items="AllOwn" v-model="item.read.allOwn"></v-select></td>
              </tr>
              <tr>
                <th>Write</th>
                <td>
                   <v-switch
                      v-model="item.write.value"
                    ></v-switch>
                </td>
                <td><v-select label="Own / All" :items="AllOwn" v-model="item.write.allOwn"></v-select></td>
              </tr>
              <tr>
                <th>Update</th>
                <td>
                   <v-switch
                      v-model="item.update.value"
                    ></v-switch>
                </td>
                <td><v-select label="Own / All" :items="AllOwn" v-model="item.update.allOwn"></v-select></td>
              </tr>
              <tr>
                <th>Delete</th>
                <td>
                   <v-switch
                      v-model="item.delete.value"
                    ></v-switch>
                </td>
                <td><v-select label="Own / All" :items="AllOwn" v-model="item.delete.allOwn"></v-select></td>
              </tr>
            </v-simple-table>
          </td>
        </template>
        <template v-slot:item.fields="{ item }" >
          <v-btn icon @click="showFields(item)"> 
            <v-icon>mdi-table-border</v-icon>
          </v-btn>
        </template>
         
      </v-data-table>

      
 
    </v-card-text>

    <v-dialog v-model="dialog" max-width="650">
      <v-card>
      
        <v-card-title>
          {{dialogTitle}} 
          <v-spacer></v-spacer>
          <v-icon @click="dialog = false">mdi-close</v-icon>
        </v-card-title>
        <v-card-text>
          <v-list>
 
            <v-list-item v-for="field in fields" :key="field.value">
              <v-list-item-icon>
                <v-switch hide-details class="ma-0" v-model="dialogFields[btoa(field.value)]"></v-switch>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  {{field.text}}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import collections from './../collections'
  export default {
    name: 'Rights',
    props: {
      label: String,
      value: {
        type: Array,
        default: () => []
      }
    },
  
    data () {
      return { 
        AllOwn: [{ text: 'All', value: 'all' }, { text: 'Own', value: 'own' }],
        // fields set
        dialog:false,
        dialogTitle: '',
        dialogFields: {},
        fields: [],


        expanded: [],
        singleExpand: false,
        selected: '',
        collections: [],
        headers: [
         { text: 'Collection', value: 'title', width: "80%", sortable: false },
         { text: 'Table Fields',  value:'fields', sortable: false },
        //  { text: 'Write', value: 'write', width: "50%", sortable: false },
        //  { text: 'Update', value: 'update', width: "50%", sortable: false },
        //  { text: 'Delete', value: 'delete', width: "50%", sortable: false },

        ],
        data: []
      }
    },
 
    methods: {
      btoa: (a) => btoa(a),
      select (collection) {
        setTimeout(() => this.selected = '', 10)
        this.data.push({ collection })
    
      },
      handelReadRight (evt, item) {
        if (!evt) {
          item.write.value = false
          item.update.value = false
          item.delete.value = false
        }
      },
      async showFields (item) {
        const collection = collections.get(item.collection)
        this.dialog = true
        this.dialogTitle = `Table Fields of (${collection.title})`
        this.fields = collection.table.filter(el => el.value != 'actions')
        this.dialogFields = this.data.filter(e => e.collection == item.collection)[0].fields
   
      }
    },
    watch: {
      data: { 
        deep: true,
        // We have to move our method to a handler field
        handler(v) {
       
          this.$emit('input', v)
        }
      }
    },
    mounted () {
      if (this.value.length < 1) {
        this.$emit('input', this.data)
      }
      
      
      this.value.forEach(e => {
        const temp = this.data.filter(el => el.value == e.value )
        if (temp[0]) {
          Object.assign(temp[0], e)
        }
      }) 
    },
    async created () {
      const models = await this.$store.dispatch('api/get', { collection: 'collections' })
        if (models && models.data)
            
          this.data = models.data.map(e => {
            const fields = {}
            const col = collections.get(e)
            if (col) {
              col.table.forEach(e => {
                if (e.value != 'actions')
                  fields[btoa(e.value)] = true
              })
  
              return { 
                collection: e,
                title: col.title,
                fields,
                read: { value: true, allOwn: 'all' },
                write: { value: true, allOwn: 'all' },
                update: { value: true, allOwn: 'all' },
                delete: { value: true, allOwn: 'all' },
              }

            }
          })
      if (this.value) {
        const data = []
        this.data.forEach(e => {
          const collection = this.value.filter(el => el.collection == e.collection)
          if (collection.length) {
            data.push(Object.assign({}, e , collection[0]))
          } else {
            data.push(e)
          }
        }) 
         
        this.data =  data
      } 
     
        
    }
  }
</script>
