<template>
 <v-menu 
   
    v-model="menu"
    :close-on-content-click="false"
    :nudge-right="40"
    transition="scale-transition"
    offset-y
    min-width="290px"
  >
    <template v-slot:activator="{ on }">
      <v-text-field
        v-bind="$attrs"
        readonly
        v-on="on"
        :value="value"
        clearable
        @click:clear="input('')"
      ></v-text-field>
    </template>
    <v-date-picker :value="value"   @input="input"  ></v-date-picker>
  </v-menu>
</template>
<script>
 

export default {
  name: 'date-picker',
  props: {
    value: String,
  },
  data() {
    return {
      menu: false,
    }
  },
  methods: {
    input (date) {
      this.menu = false
      this.$emit('input', date)
    }
  }
 
}
</script>