<template>
  <div style="width: 100%">
 
  <v-autocomplete 
  v-bind="$attrs" 
  :value="value"
  :search-input.sync="search"
  @input="emit"
  :items="dataItems"
  :loading="loading"
   @focus="makeSearch(search)"
  clearable
  :no-filter="Boolean(collection)"
  return-object
  >
   <template v-slot:prepend-item v-if="!hideAddNew && writeAccess">
       <v-list-item @click="addNew">
           
          <v-list-item-content>
            <v-list-item-title class="green--text"><v-icon color="green" >mdi-plus</v-icon> Add New</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
    </template>
    <template v-slot:item="{ parent, item }" v-if="collection == 'customer'">
      <v-list-item-content>
        <v-list-item-title>{{ item[parent.$props.itemText] }}</v-list-item-title>
        <v-list-item-subtitle >
          {{item.tpc}}
        </v-list-item-subtitle>
        <v-list-item-subtitle >
          {{ item.affiliate.name}}
        </v-list-item-subtitle>
      </v-list-item-content>
    </template>
    <template v-slot:selection="{ parent, item }" v-if="collection == 'customer'">
        <small  class="pr-1">
          ({{item.tpc}})
        </small>
       {{ item[parent.$props.itemText] }}  
        
    </template>
  </v-autocomplete>


  <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
    <v-card>
      <app-item :collection-name="collection" @close="dialog = false" @save="newSave"/>
    </v-card>
  </v-dialog>
  </div>

</template>
<script>
import debounce from 'debounce'
import { mapActions } from 'vuex'

import AppItem from './Item'
import $ from 'jquery'
export default {
  name: 'autocomplete',
  components:{
    AppItem
  },
  props: {
    value: String,
    searchFields: [String, Array],
    hideAddNew: Boolean,
    query: Object,
    collection: {
      type: String
    },
    or: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      search: '',
      dataItems: [],
      loading: false,
      dialog: false
    }
  },
  computed: {
     access () {
        if (this.$store.state.auth.user.rights) {
          return this.$store.state.auth.user.rights.filter(e => {
              return e.collection == this.collection  
          })
        } 
        return []
      },
      writeAccess () {
      
        if (this.access.length > 0)
          return this.access[0].write.value

        return false 
      },
  },
  methods: {
    emit (val) {
      this.$emit('input', val ? val[this.$attrs.itemValue] : '')
    },
    addNew () {
      this.dialog = true  
    },
    async newSave (val) {
      this.dialog = false
      this.dataItems.push(val)
      this.$emit('input', val[this.$attrs.itemValue])
      
    },
    ...mapActions({
      get: 'api/get'
    }),
    async makeSearch (value) {
      if (!this.collection) return 
      this.loading = true
      // console.log('AC -->',value);

      let query = {}
      // console.log(this.searchFields);
      if(this.searchFields) {
        if (Array.isArray(this.searchFields)) {
          this.searchFields.forEach(f => {
            if(value)
              query[f] = '^'+value
          })
        } else if(value)
          query[this.searchFields] = '^'+value
      } else if(value)
        query[this.$attrs.itemText] = '^'+value

      if (this.query && Object.keys(this.query).length > 0) {
        query = Object.assign(query, this.query)
      }
      const res = await this.get({
        collection: this.collection + '?' + $.param(query) + (this.or ? '&or=true' : '')
      })
   
      this.dataItems = res.data.docs


      this.loading = false

    }
  },
  watch: {
    search ( value ) {
      if(this.collection && value !== null)
        debounce(this.makeSearch, 500)(value)
    }
  },
  async created () {
     if(this.collection && this.value){
      const res = await this.get({ collection: this.collection, query: { 
         [this.$attrs.itemValue]: this.value
      } })
      if (res && res.data && res.data.docs) {
        this.dataItems = [...res.data.docs]
      } else if (res && res.data ){
        this.dataItems = [...res.data]
      }
     } 

     if (this.$attrs.items)
        this.dataItems = this.$attrs.items
 
      
  }
}
</script>