<template>
<v-text-field
v-bind="$attrs"
  :value="value"
  @input="$emit('input', $event)"
  autocomplete="current-password"
  :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
  :type="show ? 'text' : 'password'"
  counter
  @click:append="show = !show"
></v-text-field>
</template>
<script>
 

export default {
  name: 'password',
  props: {
    value: String,
  },
  data () {
    return {
      show: false
    }
  },

 
}
</script>